* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.row,
.col-3,
.col-6 {
  padding: 0 !important;
  margin: 0 !important;
  position: relative;
}

.calculator-body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  background-image: url('bg.png');
}
.calculator {
  width: 25%;
  height: auto;
  background-color: transparent;
  box-shadow: 0 0 15px 10px rgba(0, 0, 0, 0.274);
  position: relative;
}
.calculator-display {
  padding: 15px;
  text-align: right;
  background-color: #fafafa;
  border-radius: 15px 15px 0 0;
}
.calculator-display p {
  font-size: 1.4em;
  opacity: 0.5;
  height: 1.2em;
}
.calculator-display h1 {
  font-size: 3em;
  height: 1em;
}
.calc-btn {
  width: 100%;
  border-radius: 0;
  border: 1px solid #353738;
  padding: 20px 2px;
  background-color: #1f2326;
  color: #d7dbdd;
  font-size: 1.5em;
}
.calc-btn:focus {
  outline: none !important;
}
.calc-btn:active {
  background-color: #353738;
  box-shadow: 0 5px #666;
}
.operator-btn {
  background-color: #5f4bb6;
}
.eqal-btn {
  background-color: #ffc207;
  color: #1f2326;
}
.error-msg {
  opacity: 0.3;
  color: grey;
}
.developer {
  position: absolute;
  bottom: -15%;
  left: 50%;
  transform: translateX(-50%);
  color: grey;
  width: 100%;
  text-align: center;
}
@media (max-width: 1200px) {
  .calculator {
    width: 60%;
  }
}
@media (max-width: 800px) {
  .calculator {
    width: 80%;
  }
}
@media (max-width: 400px) {
  .calculator-body {
    height: 100vh;
  }
  .calculator {
    width: 90%;
  }
  .developer {
    bottom: -10%;
  }
}
